@import url(https://fonts.googleapis.com/css?family=Concert+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  background-color: #123328 !important;
  color: white !important;
  padding-left: 0 !important;
  position: fixed;
  min-height: 70px;
  width: 100vw;
  z-index: 1;
  /* display: block; */
  font-size: calc(14px + 14 * (100vw - 320px) / 800);
  font-family: "Concert One", cursive;
  box-shadow: 0 8px 2px -2px rgba(0, 0, 0, 0.2);
}

ul {
  margin: 0;
  width: 100%;
}

.navbar li {
  text-align: center;
  display: inline-block;
  width: 33.3%;
}

.header {
  padding-top: 8rem;
  text-align: center;
  min-height: 20rem;
  background-color: #501211;
  color: white;
  width: 100%;
  font-family: "Concert One", cursive;
  box-shadow: 0 8px 5px -2px rgba(0, 0, 0, 0.2);
}

.character {
  float: left;
  border: black 2px solid;
  margin-bottom: 1.5rem;
  margin-right: 0;
  border-radius: 4px;
  box-shadow: 0 8px 5px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.character:hover {
  position: relative;
  left: 1px;
  top: 3px;
}

.cards > .container {
  min-height: 40rem;
  max-width: 55%;
  padding-top: 1rem;
}

.footer {
  background: #123328;
  height: 90px;
  position: relative;
  color: #fff;
  overflow: auto;
  text-align: center;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
}

.caption {
  font-family: "Concert One", cursive;
  margin-top: 2rem;
}

